$(document).ready (function() {
    $(window).resize(function() {
        if ($(window).width()>=901) {
            $("body, .b-container, .b-nav").removeClass("open");
       }
    });
    $('#anim-prograss').bind('mouseenter touchstart', function() {
       $(this).find('.progress .progress-bar').progressbar({display_text: 'fill', refresh_speed: 10,  transition_delay: 300});
    });
    $('div[data-button]').on('click', function() {
        var $button= $(this);
        $button.parent().find('.text-hidden').animate({height: '180'}, 200, 'swing', function() {
            $button.animate({opacity: '0'}, 500);
        });
    });
    $('.third.circle').circleProgress({
        fill: { gradient: [['#0681c4', .5], ['#4ac5f8', .5]], gradientAngle: Math.PI / 4 }
    }).on('circle-animation-progress', function(event, progress, stepValue) {
        $(this).find('strong').text(String(stepValue.toFixed(2)).substr(2));
    });
    //$('#anim-circle').bind('mouseenter', function() {
    //    $('.third.circle').circleProgress('redraw');
    //});
    var el1 = $('#circle1'),
        inited1 = false;
    el1.appear({ force_process: true });
    el1.on('appear', function() {
        if (!inited1) {
            el1.circleProgress({value: 0.82});
            inited1 = true;
        }
    });
    var el2 = $('#circle2'),
        inited2 = false;
    el2.appear({ force_process: true });
    el2.on('appear', function() {
        if (!inited2) {
            el2.circleProgress({value: 0.2});
            inited2 = true;
        }
    });
    var el3 = $('#circle3'),
        inited3 = false;
    el3.appear({ force_process: true });
    el3.on('appear', function() {
        if (!inited3) {
            el3.circleProgress({value: 0.48});
            inited3 = true;
        }
    });
});